// Main
@use '../base/variables' as *;
@use '../utilities/functions' as *;

main {
  padding: $section-padding;

  article {
    border: 1px dotted darken($color-bg-light, 30);
    padding: em(20);
    margin-bottom: em(20);
    p:last-child {
      margin-bottom: 0;
    }
  }

  @media (min-width: em(600)) {
    section:not(.page-not-found-section) {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      > h2 {
        width: 100%;
      }
    }

    article {
      width: 49%;
      margin-bottom: 0;
    }
  }
}

.page-not-found-section {
  text-align: center;
  padding: 15rem 2rem;
  h1 {
    font-size: 3rem;
  }
  a {
    color: $color-accent;
  }
}
